form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1em;
}

label {
  white-space: nowrap;
}

input,
select {
  border: none;
  border-bottom: 1px solid var(--color);
  background-color: rgba(0, 0, 0, 0);
}
input:focus-visible,
select:focus-visible {
  outline: none;
  border-bottom: 1px solid var(--a-color);
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="checkbox"],
input[type="radio"] {
  -webkit-appearance: none;
  margin-right: 1em;
  width: 1em;
  height: 1em;
  border: 1px solid var(--color);
}
input[type="radio"] {
  border-radius: 50%;
}
select:hover,
label:hover,
input[type="checkbox"]:hover,
input[type="radio"]:hover {
  cursor: pointer;
}
input[type="checkbox"]:checked,
input[type="radio"]:checked {
  background: var(--a-color) no-repeat;
  box-shadow: inset 0px 0px 0px 3px var(--acc-color);
  /*background: url('/ligatabelle/img/haken.png') no-repeat;
  background-size: 2em;
  background-position: center;*/
}

.input-radio {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1em;
}

option {
  background-color: var(--bg-color);
  color: var(--color);
}
option:checked {
  color: var(--a-color);
}

button,
input[type="submit"],
.btn {
  padding: 0.5em;
  color: var(--a-color);
  background: var(--hf-bg-color);
  border: none;
  cursor: pointer;
}
button:hover,
input[type="submit"]:hover,
.btn:hover {
  background: var(--color);
}

.info {
  padding-bottom: 0.5em;
  font-style: italic;
}
.error {
  padding-bottom: 0.5em;
  font-style: italic;
  color: #f00;
}
.success {
  padding-bottom: 0.5em;
  font-style: italic;
  color: #0f0;
}
