@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

:root {
  --bg-color: #bbb;
  /* Default Font */
  --color: #111;
  --a-color: #f80;
  /* Header and Footer */
  --hf-color: #eee;
  --hf-bg-color: #333;
  /* Accent Color */
  --acc-color: #999;
  --sec-acc-color: #888;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg-color: #111;
    /* Default Font */
    --color: #eee;
    --a-color: #f80;
    /* Header and Footer */
    --hf-color: #eee;
    --hf-bg-color: #222;
    /* Accent Color */
    --acc-color: #444;
    --sec-acc-color: #333;
  }
}

* {
  margin: 0;
  padding: 0;
  color: var(--color);
  font-size: 1em;
  font-family: "Roboto", sans-serif;
  transition: all 1s ease;
}

body {
  background: var(--bg-color);
}

/* Desktop and mobie class *******************************/
/* Both in media query to avoid overriding */
@media only screen and (max-width: 499px) {
  .mobile {
    display: block;
  }
  .desktop {
    display: none !important;
  }
}
@media only screen and (min-width: 500px) {
  .mobile {
    display: none !important;
  }
  .desktop {
    display: block;
  }
}

/* Basic Layout */
#root {
  display: grid;
  grid:
    "head"
    "main"
    "side"
    "foot"
    / 1fr;
  grid-gap: 1em;
}

/* Fonts */
h1 {
  font-size: 2em;
}
a {
  color: var(--a-color);
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

/* Header ********************************************/
header {
  grid-area: head;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75em 2em;
  height: 50px;
  background: var(--hf-bg-color);
  color: var(--hf-color);
}

header a,
header h1 {
  color: var(--hf-color);
}
header a:hover {
  text-decoration: none;
}

header .logo {
  display: flex;
  align-items: center;
}

header .logo img {
  width: 50px;
  height: 50px;
}

header .logo h1 {
  padding-left: 0.5em;
}

header nav ul {
  display: flex;
}

header ul li {
  list-style-type: none;
  padding-left: 1em;
  font-size: 1.5em;
}

/** Content **********************************************/
#main {
  grid-area: main;
  padding: 1em;
  background: var(--acc-color);
}

aside {
  grid-area: side;
  margin-bottom: 4em;
  padding: 1em;
  background: var(--sec-acc-color);
}

/** Footer ********************************************/
footer {
  grid-area: foot;
  color: var(--hf-color);
  background-color: var(--hf-bg-color);
}

footer a {
  color: var(--hf-color);
  text-decoration: none;
}
footer a:hover {
  text-decoration: none;
}

footer.mobile {
  position: fixed;
  width: 100%;
  display: flex;
  bottom: 0;
  height: 3em;
}

footer .mobile-category-btn {
  width: calc(100% / 3);
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid var(--hf-color);
}
footer .mobile-category-btn:nth-child(1) {
  border: none;
}

footer .mobile-category-btn.active {
  color: var(--color);
  background-color: var(--acc-color);
}

/* Desktop *********************************************************/
@media only screen and (min-width: 500px) {
  #root {
    display: grid;
    grid:
      "head head"
      "main side"
      "foot foot"
      / 5fr 3fr;
    margin: auto;
    max-width: 1500px;
  }

  #main {
    margin-left: 1em;
  }

  aside {
    margin: 0 1em 0 0;
  }

  footer.desktop {
    padding: 1em;
  }
}
/* max-width has no margin at the side of #main and aside */
@media only screen and (min-width: 1500px) {
  #main,
  aside {
    margin: 0;
  }
}
